import './App.css';
import Chat from './components/chat';
import Sginin from './components/sginin';
import { dbauth } from './firebase-config';
import { useAuthState } from "react-firebase-hooks/auth"


function App() {
  const [user] = useAuthState(dbauth);
  
  return(
    <>
    {user? <Chat /> : <Sginin />}
    </>
  );
}

export default App;
