import React , {useState, useEffect,useRef} from 'react'
import "./chat.css"
import { dbauth,db } from "../firebase-config"
import { collection , addDoc } from "firebase/firestore"; 
import { query, orderBy, limit , serverTimestamp } from "firebase/firestore";  
import { useCollectionData} from "react-firebase-hooks/firestore"

export default function Chat() {
    const [sendm,setsendm] = useState("");
    const inpute = useRef(null);
    const scroller = useRef(null);


    const queryd = query(collection(db, "messages"),limit(30),orderBy("serverTimestamp"));
    
    const [messages] = useCollectionData(queryd,{idField:'id'});

    const clickhadler=async()=>{
        inpute.current.value="";
        const {photoURL,uid} = dbauth.currentUser;
        await addDoc(collection(db,"messages"),{text:sendm,serverTimestamp:serverTimestamp(),photoURL:photoURL,uid:uid});
        scroller.current.scrollTo(0,document.body.scrollHeight);
    }

    return (
        <div className="container">
            <div className="contc">
                <header>
                    <h1>Hackers Chat</h1>
                    <button className="cnbtn" onClick={()=>{dbauth.signOut()}}>Log out</button>
                </header>
                <div className="thebody">
                    <div className="ct" ref={scroller}>
                        {messages && messages.map((m)=> <div key={m.id} className={m.uid === dbauth.currentUser.uid? "me" : "notme"}><img key={m.id} src={m.photoURL}/><p>{m.text}</p></div> )}
                    </div>
                </div>
                <div className="bottom">
                    <input ref={inpute} type="text" onChange={(e)=>setsendm(e.target.value)} placeholder="Type a message"></input>
                    <button className="cnbtn" onClick={clickhadler}>send</button>
                </div>
            
            </div>
        </div>
    )
}
